import React from 'react';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';
import printJS from 'print-js';
import Icon from 'material-ui/Icon';
import IconButton from 'material-ui/IconButton';
import {Timeline, TimelineEvent} from 'react-event-timeline'
import Grow from 'material-ui/transitions/Grow';
import Skill from './Skill'

const styles = theme => ({

  input: {
    display: 'none',
  },

});

const periods = [
  {
    title:"Intern at Deansoft",
    timeRange:'2013 Jan - 2013 May',
    icon:"work",
    context:`Use <b>Qt</b> and <b>FFmpeg</b> to create a video camera live streaming decoder. <br/>
    The maximum of resolution 1080p foreach 4 camera the same time.`
  },
  {
    title: "ACM-NCPU",
    timeRange:'2014',
    icon:"bookmark_border",
    context:"3rd place"
  },
  {
    title: "Golden Shield",
    timeRange:'2015 & 2016',
    icon:"bookmark_border",
    context:"Finalist"
  },
  {
    title: "Develop at National Chiao Tung University Computer Center",
    timeRange:'2015 Jun - 2017 Jun',
    icon:"work",
    context:`  Taking part In <b>WWW</b>, <b>Linux</b> and <b>Network</b> team. Maintain <b>Arch linux</b> server and Switch in ComputerCenter.<br/>
      Project:<br/>
      <ul style={{paddingReft: "10px"}}>
        <li>Duty system</li>
        <li>Room Booking system</li>
        <li>Chiao Tung Recommend System</li>
        <li>Meeting System</li>
        <li>ELK Central Control Log System</li>
        <li>SaltStack Central Control System</li>
      </ul>`
  },
  {
    title: "Graduated from National Chiao Tung University",
    timeRange:'2015 Jun - 2017 Jun',
    icon:"school",
    context:`<b>M.S</b> Computer Science<br/>
    Software Quality Lab<br/>
    Focus on Application <b>Fuzzing</b>, <b>WebSecurity</b> and Inofmation Security`
  },
  {
    title: "QA at Trendmirco",
    timeRange:'2017 Jun - present',
    icon:"work",
    context: `In Endpoint sensor team of commercial-foundation<br/>
    <ul>
      <li>build team loacl <b>Jenkins</b> server</li>
      <li>Import <b>python3</b> into team test case</li>
      <li>write BDD automation framework (base on robotframework)</li>
      <li>manage and build local <b>ESXI</b> for test enviorment</li>
      <li>Import github flow in repo</li>
    </ul>`
  },
]

const TimelinePeriod = (props) => (
    <TimelineEvent
        title={props.data.title}
        createdAt={props.data.timeRange}
        icon={
          <Grow
            in={props.start}
            {...(props.start ? { timeout: props.index*500 } : {})}
          >
            <Icon>{props.data.icon}</Icon>
          </Grow>}
      >
      <Grow
        in={props.start}
        {...(props.start ? { timeout: props.index*500 + 250 } : {})}
      >
      <Typography
        color='default'
        dangerouslySetInnerHTML={{__html:props.data.context}}
      >
      
      </Typography>
      </Grow>
    </TimelineEvent>
)


class Resume extends React.Component {

    constructor(props) {
      super(props)
      this.state = {
        'grow':false
      }
    }
    
    componentWillMount() {
      setTimeout(()=>this.setState({grow:true}),200)
    }

    render() {
      const { classes } = this.props;
      return (
      <div>
        <Typography
          variant="headline"
          color='default'
        >
          Resume
        </Typography>
        <Grid
          container
          direction='row'
        >
          <Grid
            container
            justify='flex-end'
          >
            <button
              className={classes.input}
              id="icon-printshop"
              onClick={()=>printJS({printable:process.env.PUBLIC_URL + '/resume.html', type:'pdf'})}
            />

            <label htmlFor="icon-printshop">
              <IconButton 
                color="primary"
                className={classes.button}
                aria-label="local_printshop"
                component="span"
              >
                <Icon>local_printshop</Icon>
              </IconButton>
            </label>
          </Grid>
          
          <Grid 
            xs={6}
            item
          >
            <Typography
              variant="headline"
              color='default'
            >
              Experience
            </Typography>
            <Timeline>
              {periods.map((d,i)=><TimelinePeriod data={d} key={`${i}_periods`} start={this.state.grow} index={i+1}/>)}
            </Timeline>
          </Grid>
          <Grid 
            xs={6}
            item
          >
            <Typography
              variant="headline"
              color='default'
            >
              Skill
            </Typography>
            <Skill/>
          </Grid>
        </Grid>
      </div>)
    }
}

export default withStyles(styles)(Resume);



