import React from 'react';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';
import GridList, { GridListTile, GridListTileBar } from 'material-ui/GridList';
import skiiImg from './skillImage'
import Slide from 'material-ui/transitions/Slide';

const tileData = {
  frontend: [
    {
      img: skiiImg.react,
      title: 'React',
    },
    {
      img: skiiImg.html5,
      title: 'HTML5',
    },
    {
      img: skiiImg.css3,
      title: 'CSS3',
    },
    {
      img: skiiImg.redux,
      title: 'Redux',
    },
    {
      img: skiiImg.webpack,
      title: 'Webpack',
    },
    {
      img: skiiImg.jquery,
      title: 'Jquery',
    },
  ],
  backend: [
    {
      img: skiiImg.express,
      title: 'express',
    },
    {
      img: skiiImg.flask,
      title: 'flask',
    }
  ],
  lang: [
    {
      img: skiiImg.c,
      title: 'C/C++',
    },
    {
      img: skiiImg.java,
      title: 'java',
    },
    {
      img: skiiImg.javascript,
      title: 'javascript',
    },
    {
      img: skiiImg.python,
      title: 'python 2&3',
    },
    {
      img: skiiImg.qt,
      title: 'qt',
    }
    
  ],
  devOps: [
    {
      img: skiiImg.arch,
      title: 'Arch linux',
    },
    {
      img: skiiImg.windows,
      title: 'Win server',
    },
    {
      img: skiiImg.docker,
      title: 'Docker',
    },
    {
      img: skiiImg.vmware,
      title: 'ESXI',
    },
    {
      img: skiiImg.elasticsearch,
      title: 'ELK',
    },
    {
      img: skiiImg.cacti,
      title: 'cacti',
    },
    {
      img: skiiImg.ansible,
      title: 'Ansible',
    },
    {
      img: skiiImg.saltstack,
      title: 'saltstack',
    },
    {
      img: skiiImg.nagios,
      title: 'Nagios',
    }    
  ],


}
const direction = ['up', 'down', 'left', 'right']
const SkillPart = (props) => (
  <Grid
    item
  >
  <GridList 
    cellHeight={100}
    cols={2}
    spacing={4}
  >
    <GridListTile key="Subheader" cols={4} style={{ height: 'auto' }}>
      <Typography 
        variant="subheading"
        color='default'
      >
        {props.group}
      </Typography>
    </GridListTile>
    {props.tileData.map((tile, index) => (
      <Slide 
        direction={direction[index%4]}
        in={props.open}
        key={tile.title+'slide'}
        mountOnEnter
        unmountOnExit
        timeout={props.base + index*200}
      >
        <GridListTile key={tile.title} style={{width:"120px"}}>
          <img src={tile.img} alt={tile.title} 
          style={{
            width:"120px",
            height:"100px",
            
          }}/>
          <GridListTileBar
            title={tile.title}
            style={{
              width:"120px",
              textAlign:'center'

            }}
          />
        </GridListTile>
      </Slide>
    ))}
  </GridList>
  </Grid>
)

class Skill extends React.Component {
    constructor(props) {
      super(props)
      this.state = {
        open: false
      }
    }

    componentWillMount() {
      setTimeout(()=>this.setState({open: true}), 200)
      
    }
    
    render() {
      const { classes } = this.props;
      const speed = 100; 
      return (
      <Grid
        container
        direction='row'
      >
      <Grid
        item
        sm={12}
      >
      <SkillPart 
        classes={classes}
        tileData={tileData.frontend}
        group={'Frontend'}
        open={this.state.open}
        base={speed}
      />
      <SkillPart
        classes={classes}
        tileData={tileData.backend}
        group={'Backend'}
        open={this.state.open}
        base={speed}
      />
      <SkillPart
        classes={classes}
        tileData={tileData.lang}
        group={'Lang'}
        open={this.state.open}
        base={speed}
      />
      <SkillPart
        classes={classes}
        tileData={tileData.devOps}
        group={'DevOps'}
        open={this.state.open}
        base={speed}
      />
      </Grid>
      </Grid>
      )
    }
}

export default Skill;


