import React from 'react';
import classNames from 'classnames';
import { withStyles } from 'material-ui/styles';
import Avatar from 'material-ui/Avatar';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';


const styles = theme => ({
  
  avatar: {
    margin: 10,
  },
  bigAvatar: {
    width: 60,
    height: 60,
  },
  nameTitle: {
    width: '100%',
  },
  description: {
    
    whiteSpace:'pre-wrap'
  }
});

class AboutMe extends React.Component {
    
    render() {
      const { classes } = this.props;
      return (
      <div>
        <Typography
          variant="headline"
          color='default'
        >
          About Me
        </Typography>
        <Grid
          container
          direction='row'
        >
          <Grid item>
            <Avatar
              alt="Adelle Charles"
              src="https://en.gravatar.com/userimage/61297649/e4374c9ae29c80e05f2c1aa011a139d5.jpg"
              className={classNames(classes.avatar, classes.bigAvatar)}
              
            />
          </Grid>
          <Grid
            item
            className={classes.nameTitle}

          >
            <Typography
              variant="title"
              color='default'
            >
              <br/>
              Jian-Yu Sun ( Oscar )
            </Typography>
            <Typography
              variant="body2"
              color='default'
              className={classes.description}
            >
              I am now a develop in <b>Trendmicro</b>.<br/>
              I was awarded a Master's Degree in computer science by National Chiao Tung University.<br/>
              Interesting in secuity and web technology, like <b>fuzzing</b>, <b>react</b>, <b>revese engieer</b> ... etc.
              
            </Typography>
            <br/>
            <Typography
              variant="button"
            >
              Contact me via good0991 [at] gmail [dot] com
            </Typography>
          </Grid>
        </Grid>
      </div>)
    }
}

export default withStyles(styles)(AboutMe);


