function importAll(r) {
  return r.keys().map(r);
}
const images = importAll(require.context('./', false, /\.(png|jpe?g|svg)$/));
const infos = require('./info.json')


const imgs = infos.map(d=>{
  let match = images.filter(img=>d.title === img.split(/\./)[0].split(/\//).pop())
  if(match.length > 0)
    d.img = match[0]
  return d
})
export default imgs


