import React from 'react';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';
import GridList, { GridListTile, GridListTileBar } from 'material-ui/GridList';
import IconButton from 'material-ui/IconButton';
import imgs from './projectImage'
import Modal from 'material-ui/Modal';
import Zoom from 'material-ui/transitions/Zoom';


const styles = theme => ({
  
  gridList: {
    width: 800,
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },

  paper: {
    position: 'absolute',
    width: theme.spacing.unit * 100,
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[4],
    padding: theme.spacing.unit * 4,
    whiteSpace: 'pre-wrap',
    top: '50%',
    left: '50%',
    transform: `translate(-55%, -55%)`,
  }
});


class Project extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      title: "none",
      detail: "none",
      framework: "none",
      open: false,
      zoom:false,
    }
  }
  componentWillMount() {
    setTimeout(()=>this.setState({zoom:true}),200)
  }
  render() {
    const { classes } = this.props;
    return (
    <div>
      <Typography
        variant="headline"
        color='default'
      >
        Project
      </Typography>

      <Grid
        container
        direction='row'
        justify="center"
      > 
        <Grid
          item
          
        >
        
          <GridList
          cellHeight={180}
          cols={4}
          className={classes.gridList}
          spacing={6}
          >

            {imgs.map((info, i) => (
              <Zoom 
                key={info.title + 'Zoom'} 
                in={this.state.zoom}
                style={{ transitionDelay: this.state.zoom ? 200 + 100*i : 0 }}
              >
                <GridListTile key={info.title}>
                  <img src={info.img} alt={info.title} />
                  <GridListTileBar
                    title={info.title}
                    subtitle={<span>{info.desc}</span>}
                    
                    actionIcon={
                      <IconButton
                        className={classes.icon}
                        onClick={()=>this.setState({
                          open:true,
                          ...info
                        })}
                      >
                        <i className="material-icons">subject</i>
                      </IconButton>
                    }
                  />
                </GridListTile>
              </Zoom>
            ))}
          </GridList>
        </Grid>
      </Grid>
      <Modal
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        open={this.state.open}
        onClose={()=>this.setState({open: false})}
        
      >
        <div className={classes.paper}>
          <Typography variant="title" id="modal-title">
            {this.state.title}
          </Typography>
          <img height="100%" width="100%" src={this.state.img} alt={this.state.title}/>
          <Typography variant="skill" id="modal-subtitle">
            use skill or framework:
          </Typography>
            <ul>
              {this.state.framework.split('&').join('\n')}
            </ul>
            
          <Typography variant="subheading" id="simple-modal-description">
            {this.state.etc}
          </Typography>
        </div>
      </Modal>
      
    </div>)
  }
}

export default withStyles(styles)(Project);



