import React from 'react';
import Grid from 'material-ui/Grid';

class GuttersGrid extends React.Component {
  
  render(){
    return (
      <Grid
        container
        alignItems='center'
        direction='row'
        justify='center'
      >
        
        <Grid item xs={10}>
          {this.props.children}
        </Grid>
        
      </Grid>
    );
  }
}

export default GuttersGrid;



